.buy-back_tag {
  background: rgba(15, 181, 184, 0.1);
  padding: 4px 16px;
  border-radius: 8px;
}

.search-input {
  height: 32px !important;
  border-color: rgba(33, 36, 51, 0.1) !important;
  background: transparent !important;
  input {
    background: transparent !important;
  }
}

.countdown {
  padding: 4px 8px;
  background: #e9e9eb;
  border-radius: 4px;
}

#dark {
  .search-input {
    border-color: rgba(255, 255, 255, 0.2) !important;
  }
  .countdown {
    background-color: #212433 !important;
  }
}

@ant-prefix: sen_booster;